<template>
  <section class="hero">
    <div class="hero-body">
      <p class="title">Peikai Tan</p>
      <p class="subtitle">Singapore PSI Plot (OneMap)</p>
    </div>
  </section>
  <div class="container">
    <div class="columns">
      <div class="column">
        <div class="card-content">Updated: {{ updated }}</div>
        <div class="card-content">
          <button class="button" @click="fetchData">Update</button>
        </div>
      </div>
      <div class="column is-three-fifths">
        <div id="mapdiv" style="height: 550px"></div>
      </div>
      <div id="tooltip" class="column">
        <div class="card-content" v-if="hovered">
          Region: {{ name }}
          <br />
          PM10 sub index: {{ pm10 }}
          <br />
          CO sub index: {{ co }}
          <br />
          SO2 sub index: {{ so2 }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import * as d3 from "d3";
import * as L from "leaflet";

export default {
  name: "App",
  data() {
    return {
      map: null,
      markerLayer: null,
      updated: null,
      hovered: null,
      pm10: null,
    };
  },
  methods: {
    buildCircleSVG(psi) {
      return (
        '<svg height="50" width="50"><circle cx="25" cy="25" r="20" stroke="black" stroke-width="3" fill="white"/>' +
        '<text x="50%" y="50%" text-anchor="middle" dy=".3em">' +
        psi +
        "</text></svg>"
      );
    },

    fetchData() {
      this.markerLayer.clearLayers();
      d3.json("https://api.data.gov.sg/v1/environment/psi").then((data) => {
        this.updated = Date(data.items[0].update_timestamp);
        let coordinates = Object.assign(
          {},
          ...data.region_metadata.map((d) => ({
            [d.name]: [d.label_location.latitude, d.label_location.longitude],
          }))
        );

        let PSIs = data.items[0].readings.psi_twenty_four_hourly;

        console.log(data);

        for (const name in PSIs) {
          let circle = this.buildCircleSVG(PSIs[name]);
          let myIcon = L.divIcon({
            className: "svgIcon",
            iconSize: [50, 50],
            html: circle,
          });
          let marker = L.marker(coordinates[name], {
            icon: myIcon,
          }).addTo(this.markerLayer);

          marker.on("mouseover", () => {
            this.hovered = true;
            this.name = name;
            this.pm10 = data.items[0].readings.pm10_sub_index[name];
            this.co = data.items[0].readings.co_sub_index[name];
            this.so2 = data.items[0].readings.so2_sub_index[name];
          });

          marker.on("mouseout", () => {
            this.hovered = null;
          });

          marker.bindPopup("omg! this is not a typo.");
        }
      });
    },
  },
  components: {},
  mounted() {
    var center = L.bounds([1.56073, 104.11475], [1.16, 103.602]).getCenter();
    this.map = L.map("mapdiv", {
      zoomControl: false,
    }).setView([center.x, center.y], 11);

    var basemap = L.tileLayer(
      "https://maps-c.onemap.sg/v3/Grey/{z}/{x}/{y}.png",
      {
        detectRetina: true,
        maxZoom: 11,
        minZoom: 10,
      }
    );

    this.map.setMaxBounds([
      [1.46073, 104.1147],
      [1.16, 103.602],
    ]);

    basemap.addTo(this.map);

    this.markerLayer = L.layerGroup().addTo(this.map);

    this.fetchData();
  },
};
</script>

<style>
#tooltip {
  text-transform: capitalize;
}

body {
  height: 100vh;
  background-color: #D1D1D1;
}
</style>
